.project_popup {
    position: fixed;
    top: 50%;
    left: 50%;
    transition: 200ms ease-in-out;
    transform: translate(-50%, -50%);
    border: 2px solid white;
    border-radius: 2px;
    background-color:  #96C0CE;
    z-index: 22;
    width: 80%;
    height: 70%;
}

.popup_header {
    padding-top: 20px;
    text-align: center;
    color: black;
}

.popup_body {
    padding-left: 60px;
    padding-right: 70px;
    text-align: justify;
    color: black;
}

a {
    color: white;
}

.close_button {
    position: absolute;
    top: 0;
    right: 0;
    padding-right: 15px;
    font-size: 40px;
    font-weight: bold;
    color: black;
    background: none;
    border: none;
    cursor: pointer;
}

.close_button:active,
.close_button:hover {
    color: white;
}
