.project_box {
    background: none;
}

.project_box h3 {
    padding-left: 25px;
}


.project_box .box {
    position: relative;
    width: 330px;
    height: 330px;
    background: #96C0CE;
    float: left;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 8px 8px 4px -2px rgba(0,0,0,.2);
}

.project_box .box .search_overlay {
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: rgba(76, 112, 209, 0.4);
    visibility: hidden;
    cursor: pointer;
}

.project_box .box:hover .search_overlay {
    visibility: visible;
}

.project_box .box .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-10%, -10%);
    width: 100%;
    height: 100%;
    transition: 0.5s;
    z-index: 1;
    visibility: hidden;
    cursor: pointer;
}

.project_box .box:hover .fas {
    visibility: visible;
    color: whitesmoke;
}
