
.header {
    overflow: hidden;
    background-color: #96C0CE;
    box-shadow: 0 4px 4px -2px rgba(0,0,0,.35);
    padding: 20px 10px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 8;
}

.header a, .title {
    float: left;
    color: black;
    text-align: center;
    color: white;
    padding: 12px;
    text-decoration: none;
    font-size: 18px;
    line-height: 25px;
    border-radius: 4px;
}

.title {
    font-size: 35px;
    font-weight: bold;
    text-transform: uppercase;
}

.header a:hover {
    background-color: rgba(255,255,255, .7);
    color: black;
}

.header-right {
    float: right;
    padding-right: 20px;
}
